*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Epunda Slab helper class (uses system/browser font stack or any loaded Epunda font)
   Replace <uniquifier> with a unique suffix if you need multiple variants.
*/
.epunda-slab-typed {
  font-family: "Epunda Slab", serif;
  font-optical-sizing: auto;
  font-weight: 300;
  font-style: regular;
}

html, body, #root {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

/* Visual highlight used when a sidebar nav target is focused/navigated to */
.sidebar-nav-highlight {
  box-shadow: 0 0 0 6px rgba(230,126,34,0.08) inset, 0 8px 28px rgba(230,126,34,0.06);
  transition: box-shadow 320ms ease;
}

/* Disable scroll snapping globally to avoid instant jumps */
html, body, #root, #content-container {
  scroll-snap-type: none !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* subtle gradient wash behind every section to create consistent accents */
section {
  position: relative; /* ensure the pseudo-element positions correctly */
}

section::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: inherit;
  /* very subtle two-tone gradient to add depth without altering card colors */
  background: linear-gradient(135deg, rgba(99,102,241,0.06), rgba(14,165,164,0.03));
  filter: blur(12px);
  transform: translateY(6px) scale(1.01);
  z-index: -1;
  pointer-events: none;
  opacity: 0.95;
}
